class App extends React.Component {
  constructor(props){
      super(props)
      this.state = {
          location:"/"
      }
      this.setLocation = (target)=>{
          this.setState({location:target})
          window.history.pushState(null,null,"#/"+target)
      }
  }
  componentDidMount(){
      this.setState({location:window.location.hash.split("/")[1]||""})
      window.addEventListener("popstate",()=>{
          this.setState({location:window.location.hash.split("/")[1]||""})
      })
      document.documentElement.style.setProperty('--vh',`${window.innerHeight*0.01}px`);
        window.addEventListener('resize',()=>{
            document.documentElement.style.setProperty('--vh',`${window.innerHeight*0.01}px`);
        });
      }
  componentDidUpdate(){
  }
  render(){
      return(
          <React.Fragment>
              <Home location={this.state.location} setLocation={(target)=>this.setLocation(target)}/>
                <TransitionGroup>
                  <CSSTransition key={this.state.location} timeout={500}>
                    <Project slug={this.state.location} setLocation={(target)=>this.setLocation(target)}/>
                  </CSSTransition>
                </TransitionGroup>    
          </React.Fragment>
      )
  }
}

export default App

class Block extends React.Component {
  render(){
      if(this.props.type==="title"){
          return(
              <h1>{this.props.content}</h1>
          )
      }
      if(this.props.type==="text"){
          return(
              <section dangerouslySetInnerHTML={{
                  __html: this.props.content
              }}></section>
          )
      }
      if(this.props.type==="repeater"){
          return(
              <section style={{margin: "0",maxWidth: "calc(800px + 2rem)"}}>
                  <div className="image-repeater" style={{backgroundImage:`url('${this.props.content.s}')`,width:this.props.content.w}}/> 
              </section>
          )
      }
      if(this.props.type==="footer"){
          return(
              <footer>
                  <div className="l">
                      <video src={this.props.content.v} autoPlay muted loop playsInline></video>    
                  </div>
                  <div className="r">
                      <div>Yuhao Yang</div>
                      <div>{this.props.content.d}</div>    
                  </div>
              </footer>
          )
      }
      return(<React.Fragment/>)
  }
}

class Project extends React.Component {
  constructor(props){
      super(props)
      this.data = []
  }
  componentDidMount(){
      
  }
  componentDidUpdate(){       
           
  }
  render(){
      this.data = data.pages.filter((item)=>{
          return item.slug === this.props.slug
      })
      if(this.data.length){
          return(
            <>
              <div className="project" style={this.props.style}>
                  <article>
                      {this.data[0].c.map(block=>(
                          <Block id={block.id} type={block.t} content={block.c}/>
                      ))}
                  </article>
              </div>
              <div className="back" onClick={()=>this.props.setLocation("")}>BACK</div>
            </>
          )
      }
      return(<React.Fragment/>)
  }
}

class Home extends React.Component {
  constructor(props){
      super(props)
      this.state = {
          entered: false
      }
      this.timer = null;
  }
  componentDidMount(){
      this.timer = setTimeout(() => {
          this.setState({entered:true})
      }, 200);
  }
  render(){
      return(
          <div className="start" style={{opacity:this.state.entered&&this.props.location===""?"1":"0",pointerEvents:this.props.location===""?"visible":"none"}}>
              {
                  data.nav.map((n,j)=>(
                      <h1 id={n.id}><a myhref={n.l} onClick={j>1?()=>this.props.setLocation(n.l):null}>{n.c.split("").map((l,i)=>(
                          <span t={(j+i)%2===0?j>1?"1":null:null}>{l}</span>
                      ))}</a></h1>
                  ))
              }
          </div>
      )
  }
}
